import { writable } from 'svelte/store';
import { GameStatus } from '../components/App/types';
class MainStore {
    constructor(data) {
        this.store = writable(data);
    }
    setGameStatus(gameStatus) {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { status: gameStatus })));
    }
    setLobby(gameStatus, lobby) {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { status: gameStatus, currentLobby: lobby || undefined })));
    }
    setGameMatrix(matrix) {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { status: GameStatus.GAME, gameMatrix: matrix })));
    }
    setFireTurn(fireTurn) {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { fireTurn: fireTurn })));
    }
    setError(message) {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { error: message })));
    }
    reset() {
        this.store.set({
            status: GameStatus.MAIN
        });
    }
}
export { MainStore };
const mainStore = new MainStore({
    status: GameStatus.MAIN
});
export default mainStore;
