import { writable } from 'svelte/store';
import { buildValidator } from './validate';
const createFieldValidator = (...validators) => {
    const { subscribe, set } = writable({ dirty: false, valid: false, message: undefined, touch: false });
    const validator = buildValidator(validators);
    const action = (node, binding) => {
        let touch = false;
        const onFocus = () => {
            touch = true;
        };
        node.addEventListener('focus', onFocus);
        function validate(value, dirty, touch) {
            const result = validator(value, dirty, touch);
            set(result);
        }
        validate(binding, false, false);
        return {
            update(value) {
                validate(value, true, touch);
            }
        };
    };
    return {
        data: { subscribe },
        validate: action
    };
};
export default createFieldValidator;
