import { get, writable } from 'svelte/store';
class SetShips {
    constructor(lobby) {
        const lobbyData = get(lobby);
        this.store = writable({
            ships4n: lobbyData.ships4n,
            ships3n: lobbyData.ships3n,
            ships2n: lobbyData.ships2n,
            ships1n: lobbyData.ships1n,
            isReady: false,
            opponentReady: false,
        });
    }
    decraseShip(shipSize) {
        this.store.update((store) => {
            // @ts-expect-error: TS can associate state with dynamic object
            store[`ships${shipSize}n`] -= 1;
            return store;
        });
    }
    increseShip(shipSize) {
        this.store.update((store) => {
            // @ts-expect-error: TS can associate state with dynamic object
            store[`ships${shipSize}n`] += 1;
            return store;
        });
    }
    setReady() {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { isReady: true })));
    }
    setOpponentReady() {
        this.store.update((store) => (Object.assign(Object.assign({}, store), { opponentReady: true })));
    }
}
export default SetShips;
